/*doc
---
title: 
name: text-unit
category: text
---


```html_example

<div class="text-unit">
  <p> </p>
  <p></p>
</div>

```

*/

.text-unit {
  *:first-child{
    margin-top: 0;
  }
    p {
      font-size: 1em;
      margin-bottom: 2em;
    }
    *:last-child {
      margin-bottom: 0;
    }
    
  em {
    color: #f42d49;
    font-style: normal;
    font-weight: bold;
  }
}

.entry > *:first-child{
  margin-top: 0;
}
.entry-unit > *:first-child{
  margin-top: 0;
}