/* ============================================================ //
// @ form
// ============================================================ */

.form-group{
	margin-bottom: 0;
}

.label--required{
	display: inline-block;
	position: relative;
	top:-2px;
	border-radius: 10px;
	background: #fff;
	padding:.4em .5em;
	line-height: 1;
	font-size: 12px;
	color: #108766;
	font-weight: normal;
}

.fld-agree__text{
	font-size: 16px;
	font-weight: normal;
}

/* ============================================================ //
// @ validationEngine.jquery.css
// ============================================================ */



/* Z-INDEX */
.formError { z-index: 990; }
.formError .formErrorContent { z-index: 991; }
.formError .formErrorArrow { z-index: 996; }

.ui-dialog .formError { z-index: 5000; }
.ui-dialog .formError .formErrorContent { z-index: 5001; }
.ui-dialog .formError .formErrorArrow { z-index: 5006; }


.inputContainer {
	position: relative;
	float: left;
}

.formError {
	position: absolute;
	top: 300px;
	left: 300px;
	display: block;
	cursor: pointer;
	text-align: left;
}

.formError.inline {
	position: relative;
	top: 0;
	left: 0;
	display: inline-block;
}

.ajaxSubmit {
	padding: 20px;
	background: #55ea55;
	border: 1px solid #999;
	display: none;
}

.formError .formErrorContent {
	width: 100%;
	background: #ee0101;
	position:relative;
	color: #fff;
	min-width: 120px;
	font-size: 13px;
	opacity:.8;
	//box-shadow: 0 0 6px #000;
	//-moz-box-shadow: 0 0 6px #000;
	//-webkit-box-shadow: 0 0 6px #000;
	//-o-box-shadow: 0 0 6px #000;
	padding: .5em 1em;
	border-radius: 3px;
}

.formError.inline .formErrorContent {
	box-shadow: none;
	-moz-box-shadow: none;
	-webkit-box-shadow: none;
	-o-box-shadow: none;
	border: none;
	border-radius: 0;
	-moz-border-radius: 0;
	-webkit-border-radius: 0;
	-o-border-radius: 0;
}

.greenPopup .formErrorContent {
	background: #33be40;
}

.blackPopup .formErrorContent {
	background: #393939;
	color: #FFF;
}

